

* {
  font-family: monospace; 
  color:white;
}

html,body,*{
  margin:0;
  padding:0;
}



html {
  width: 100vw;
  height:100vh;
}


body, #root{
  width:100%;
  height:100%;
}

#root {
  height:100%;
}
.App {
  text-align: center;
  width:100%;
  height:100%;
  overflow-x:hidden;
}



.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.background
{
  position:fixed;
  top:0;
  left:0;
  overflow:hidden;
  width:100%;
  height:100%;
  z-index:-9999;
}
.home
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    padding:40px;
    align-items:center;  
    box-sizing: border-box;
}




.home-text
{
  font-size:50px;
}

.home-text > * 
{
  text-align:left;
}


.qualif
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-end;
    padding:40px;
    align-items:center;  
    box-sizing: border-box;
}


.qualif_box
{

  width:600px;
  background-color:rgba(255,255,255,0.3);
  border-radius:3px;
  padding:10px;

}

.qualif_title
{
  font-size:30px;
}

.qualif_box_content
{
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items: flex-start;
  flex-wrap:wrap;
}

.qualif_box_sub_item
{
  padding-left:10px;
  text-align:left
}

.qualif_box_item
{
  text-align:left
}
